import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import foto1 from "../../images/sliced-green-fruits-2171077.jpg"

const Ubuntu = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="¿Realmente necesitas tomar una vitamina?"
                meta="En la industria de los suplementos, que en gran medida no está reglamentada, los productos pueden ser ineficaces o incluso peligrosos."
                description="En la industria de los suplementos, que en gran medida no está reglamentada, los productos pueden ser ineficaces o incluso peligrosos."
                lang="es"
            />
            <h1>¿Realmente necesitas tomar una vitamina?</h1>
            <hr/>
            <p class="introductionText">En la industria de los suplementos, que en gran medida no está reglamentada, los
                productos pueden ser ineficaces o incluso peligrosos.</p>
            <hr/>
            <p></p>
            <div>
                <h2>Visi&oacute;n general</h2> <p>Los suplementos de vitaminas y minerales pueden ser costosos. Tomarlos con
                    regularidad puede hacerte sentir que est&aacute;s llevando un estilo de vida m&aacute;s saludable.
                    <b>Pero varios estudios de investigaci&oacute;n sugieren que los suplementos no siempre son
                    beneficiosos. Tomar ciertos suplementos de vitaminas y minerales puede incluso hacer m&aacute;s
                        da&ntilde;o que bien.</b></p>
                <p>Para algunas personas, los suplementos de vitaminas y minerales ofrecen importantes beneficios para
                    la salud. Si tiene ciertas condiciones o necesidades de salud, su m&eacute;dico puede sugerirle que
                    a&ntilde;ada un suplemento a su rutina diaria. Pero las personas que toman suplementos como
                    "p&oacute;liza de seguro" contra los malos h&aacute;bitos alimenticios pueden aumentar su riesgo de
                    problemas de salud.</p>
                <p>Entonces, &iquest;c&oacute;mo sabe qu&eacute; es lo correcto para usted? La mejor manera es hablar
                    con su m&eacute;dico antes de tomar suplementos diet&eacute;ticos. Si ya est&aacute; tomando
                    suplementos, preg&uacute;ntele si es una buena opci&oacute;n para continuar. Adem&aacute;s de
                    aumentar el riesgo de padecer ciertos problemas de salud, algunos suplementos pueden interactuar con
                    los medicamentos que est&aacute; tomando.</p>
                <div style={{textAlign:`center`,margin:'50px'}} >
                    <img src={foto1} style={{width:`50vh`}} alt="fruta"></img>
                </div>
                <h2>M&aacute;s no siempre es mejor</h2><p>Algunos estudios han vinculado los suplementos de
                    hierro con un mayor riesgo de muerte.<br/>Los suplementos de vitamina E pueden aumentar el riesgo de
                    insuficiencia card&iacute;aca.<br/>Demasiada vitamina A puede ser mala para los huesos.<br/>Si una
                    peque&ntilde;a cantidad de algo es buena, podr&iacute;a pensar que una cantidad mayor ser&iacute;a
                    a&uacute;n mejor. Pero esa f&oacute;rmula no siempre funciona cuando se trata de vitaminas y
                    minerales.</p>
                <p>Los investigadores del Estudio de Salud de la Mujer de Iowa hicieron un seguimiento a m&aacute;s de
                    38.000 mujeres, de 55 a&ntilde;os o m&aacute;s, durante un per&iacute;odo de 20 a&ntilde;os. De
                    acuerdo con los resultados publicados en los Archivos de Medicina Interna - Fuente Confiable,
                    encontraron que la mayor&iacute;a de los suplementos de vitaminas y minerales no estaban asociados
                    con un menor riesgo de muerte durante el estudio. Los suplementos de calcio se asociaron con un
                    riesgo ligeramente menor de muerte. Pero otros suplementos de uso com&uacute;n, especialmente el
                    hierro, se relacionaron con un mayor riesgo de muerte.</p>
                <p>Esta investigaci&oacute;n no significa que el hierro y otras vitaminas y minerales sean malos para
                    usted. Usted necesita tener hierro en su dieta y en su cuerpo para estar saludable. Y para las
                    personas con ciertas condiciones m&eacute;dicas, como la anemia, los suplementos de hierro suelen
                    ser vitales. Pero este estudio sugiere que para las personas saludables, tomar hierro extra en forma
                    de suplementos puede causar da&ntilde;o.</p>
                <p>Otros suplementos vitam&iacute;nicos y minerales tambi&eacute;n pueden hacer m&aacute;s da&ntilde;o
                    que bien. <b>De acuerdo con la Cl&iacute;nica Mayo, las investigaciones sugieren que el consumo de
                    suplementos de vitamina E puede aumentar el riesgo de insuficiencia card&iacute;aca y muerte
                    prematura.</b> La Cl&iacute;nica Mayo tambi&eacute;n advierte que tomar m&aacute;s de 200 miligramos de
                    vitamina B-6 al d&iacute;a puede causar dolor nervioso y convulsiones. Investigaciones recientes
                    reportadas por los Institutos Nacionales de Salud tambi&eacute;n sugieren que demasiada vitamina A
                        puede ser mala para los huesos.</p>
                <p>Hable con su m&eacute;dico para conocer m&aacute;s sobre los riesgos y beneficios potenciales de los
                    suplementos de vitaminas y minerales.</p>
                <h2>Los suplementos no son m&aacute;gicos</h2><p>Es importante recordar que los suplementos
                    diet&eacute;ticos no pueden sustituir una dieta bien equilibrada. Algunas personas creen que tomar
                    una p&iacute;ldora multivitam&iacute;nica puede compensar los malos h&aacute;bitos alimenticios. En
                    realidad, los suplementos de vitaminas y minerales no ofrecen una soluci&oacute;n m&aacute;gica.</p>
                <p><b>Si sospecha que no est&aacute; obteniendo los nutrientes que necesita, considere la posibilidad de
                    cambiar su enfoque de los suplementos a una alimentaci&oacute;n mejor. Seg&uacute;n la
                    Cl&iacute;nica Mayo, los alimentos integrales ricos en nutrientes -como las frutas, las verduras y
                    los granos integrales- proporcionan muchos beneficios en comparaci&oacute;n con los suplementos
                    diet&eacute;ticos:</b></p>
                <ul><li>Los alimentos integrales contienen m&uacute;ltiples micronutrientes que pueden trabajar juntos para
                    proporcionar m&aacute;s beneficios que por separado.</li><li>Muchos alimentos integrales son fuentes
                    ricas en fibra alimentaria. Una dieta rica en fibra puede ayudar a reducir el riesgo de muchas
                    condiciones de salud, incluyendo el estre&ntilde;imiento y las enfermedades card&iacute;acas.</li><li>Muchos
                    alimentos integrales tambi&eacute;n contienen fitoqu&iacute;micos. Estas sustancias pueden ayudar a
                    protegerlo contra las enfermedades card&iacute;acas, la diabetes, el c&aacute;ncer y otras
                    condiciones de salud.</li></ul><p>Para obtener consejos de alimentaci&oacute;n saludable, hable con su
                    m&eacute;dico o con un dietista registrado.</p>
            </div>

        </Layout>
    )
}

export default Ubuntu
